<template>
  <div id="appMain">
    <div class="main-container"> 
      <Chatbox v-bind:ry_message_thread="ry_message_thread" :tagname="tagname"/>   
     <Inputbox v-on:add-ry-message="addRyMessage"/> 
      <Toppanel/>
    
    </div>
  </div>
</template>

<script>
import Chatbox from './AppContents/Chatbox'
import Toppanel from './AppContents/Toppanel'
import Inputbox from './AppContents/Inputbox'
import firebase from 'firebase'

import {v4 as uuidv4} from 'uuid'

export default {
  name: 'Appchat',
  components: {
    Toppanel,
    Chatbox,
    Inputbox
    
  },
  data(){
    return{
      ry_message_thread:[],
      tagname:'',
      con_id:this.$route.params.con_id
    }
  },
  watch:{
    '$route'(to,from){
      this.con_id=to.params.con_id;
      console.log('oldparam:',from.params.con_id)
    }
  },
  created(){
    let user = firebase.auth().currentUser;
    let fs = firebase.firestore();
    fs.collection("convos").doc(this.con_id).onSnapshot((doc)=>{
       if(doc.exists){
         var auth_keys = doc.data().auths;
         if(auth_keys.includes(user.uid)){
           this.ry_message_thread=doc.data().mainThread;
           fs.collection("users").doc(user.uid).onSnapshot((doc)=>{
                if(doc.exists){
                      this.tagname=doc.data().r_nametag;
                 }else{
                  console.log('failed to parse')
                }
                });
         }else{
           this.$router.replace({path: '/rhymeapplobby' });
         }
       }else{
         console.log('failed to parse')
       }
     });
  }
  ,
  methods:{
    delRyMessage(id){
      this.ry_message_thread = this.ry_message_thread.filter(m=>m.id!==id); 
    },
    async addRyMessage(rymessage){
      
      let fs = firebase.firestore();
      var new_key = uuidv4();
      var rymessage_final = {id:new_key,r_mess_thread:{nametag:this.tagname,r_text:rymessage}};
      let newMessageThread = fs.collection("convos").doc(this.con_id);
      await newMessageThread.update({
                                    mainThread: firebase.firestore.FieldValue.arrayUnion(rymessage_final)
                                    }).catch((error)=>{
                                        console.log("Error getting documents: ", error);
                                    });  
    }

  }
}
</script>

<style scoped>
  
  .main-container{
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    
  }
  

</style>
