<template>
  <div class="inputbox">
      <input type="text" autocomplete="off" placeholder=">" v-on:keyup="keymonitor" v-model="ry_message" id="mess-inp" class="mess-inp"> 
      <button id="send" class="send" @click="add_ry_message"><div>></div></button>
  </div>
  
</template>

<script>

export default {
  name: 'Inputbox',
  props: [],
  data(){
      return{
           ry_message:''
      }
       },
    methods:{
        keymonitor:function(e){
            if(e.key == "Enter"){
                
                if(this.ry_message!=''){
            const ry_message = this.ry_message;
            
            //sending to parent
            this.$emit('add-ry-message',ry_message);
            this.ry_message = '';
            }
            }
        },
        add_ry_message(){
            if(this.ry_message!=''){
             
            const ry_message = this.ry_message;
            
            //sending to parent
            this.$emit('add-ry-message',ry_message);
            this.ry_message = '';
            }
        }

    }
}
</script>

<style scoped>
.inputbox{
    
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 3rem;
    background-color: #2e2a4e;
    
    
    /* background-color: forestgreen; */
    display: flex;
    flex-direction: row;   
    justify-content:center;
    
}
.mess-inp{
    width: 90%;
    height: 2rem;
    margin-left: 0.5rem;
    margin-right: 3rem;
    margin-top: 0.5rem;
    border:0;
    background-color: #25223f;
    font-family: monospace;
    padding-left: 1rem;
    padding-right: 2rem;
    outline: 0;
    font-size: 1.2rem;
    color: #eeeeee;
    
}
.send{
    width: 3rem;
    height: 3rem;
    background-color:#2e2a4e;
    border:0;
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    outline:0;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.send > div{
    width:1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    border:1px solid #e5ddac;
    color:floralwhite;
    font-family: monospace;
    line-height: 1.2em;
    font-size: 1.5em;
    
}

.send:hover{
    background-color: #35305a;
}
</style>
